<template>
  <div style="text-align:left;">
    <div>
      您拥有
      <font style="color:blue;">{{ nftPibnxBalanceEffect }}</font> 个有效PiBNX
      ，{{ nftPibnxBalance - nftPibnxBalanceEffect }} 个已过期PiBNX。<br />
      可批量管理
      <font style="color:blue;">{{ nftPibnxBalanceEffect * 100 }}</font>
      个英雄,已有<font style="color:blue;">{{ heroBalance }}</font
      >个英雄。<br />需要购买
      <font style="color:blue;">{{ AmountNeedBuyPiBNX }}</font> 个PiBNX
    </div>
    <el-input-number
      ref="nft-amount"
      v-model="amount"
      placeholder="1-20"
      :min="1"
      :max="20"
      :step="5"
      style="width:180px;"
    ></el-input-number>
    <el-button type="primary" @click.prevent="mint" :loading="isMinting"
      >购买</el-button
    >
    <p style="color:grey;">
      <label>每个PiBNX售价为 </label
      ><font style="color:blue"> {{ nftPibnxPrice }}</font> BNB,每次可购买<font
        style="color:blue"
        >1</font
      >-<font style="color:blue">20</font>个
    </p>
    <div>
      <el-table :data="nftPibnxTokens" stripe>
        <el-table-column label="序号" width="50">
          <template #default="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column label="NFT" width="300">
          <template #default="scope">
            <img :src="tokenURIImage(scope.row.tokenURI)" />
          </template>
        </el-table-column>
        <el-table-column label="tokenId" width="100">
          <template #default="scope">
            {{ scope.row.tokenId }}
          </template>
        </el-table-column>
        <el-table-column label="购买区块高度" width="100">
          <template #default="scope">
            {{ scope.row.birthBlock }}
          </template>
        </el-table-column>
        <el-table-column label="剩余时长" width="150">
          <template #default="scope">
            {{ remainTimeLens(scope.row.remainBlock) }}
          </template>
        </el-table-column>
      </el-table>
      <div v-if="false">
        <img style="width:200px;height:200px;" :src="nftImage" />
      </div>
    </div>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
export default {
  data: function() {
    return {
      amount: 5,
      isMinting: false,
      nftImage: "",
      nftSvg:
        "data:application/json;base64,",
    };
  },
  computed: {
    ...mapGetters([
      "nftPibnxBalance",
      "nftPibnxBalanceEffect",
      "nftPibnxTokens",
      "nftPibnxPrice",
      "heroBalance",
    ]),
    AmountNeedBuyPiBNX: function() {
      const heros = this.heroBalance - this.nftPibnxBalanceEffect * 100;
      if (heros < 1) {
        return 0;
      }
      if (heros < 100) {
        return 1;
      }
      return Math.ceil(heros / 100);
    },
  },
  watch: {
    AmountNeedBuyPiBNX: function(val) {
      if (val >= 1 && val <= 20) {
        this.amount = val;
      }
    },
  },
  mounted: function() {
    // this.getSvg();
  },
  methods: {
    tokenURIImage: function(tokenURI) {
      const j = JSON.parse(
        Buffer.from(tokenURI.substring(29), "base64").toString()
      );
      return j.image;
    },
    getSvg: function() {
      const b64 = this.nftSvg.substring(29);
      console.log("b64=", b64);
      const jsonC = Buffer.from(b64, "base64").toString();
      console.log(jsonC);
      const jsonO = JSON.parse(jsonC);
      console.log(jsonO);
      console.log(jsonO.image);
      this.nftImage = jsonO.image;
    },
    remainTimeLens: function(remainBlock) {
      if (remainBlock <= 0) {
        return `已过期`;
      }
      if (remainBlock > 20 * 60 * 24) {
        const days = Math.floor(remainBlock / (20 * 60 * 24));
        const hours = ((remainBlock % (20 * 60 * 24)) / (20 * 60)).toFixed(2);
        return ` ${days}天 ${hours}小时 `;
      } else {
        const hours = (remainBlock / (20 * 60)).toFixed(2);
        return ` ${hours}小时 `;
      }
    },
    mint: async function() {
      console.log("mint amount=", this.amount);
      if (this.amount == undefined || this.amount == 0 || this.amount > 20) {
        ElMessage.warning("购买数量不对[1-20]");
        this.$refs["nft-amount"].focus();
        return;
      }
      this.isMinting = true;
      await this.$store
        .dispatch("pibnx/mint", { amount: this.amount })
        .then(() => {
          this.isMinting = false;
          ElMessage.success("success");
          this.$store.dispatch("pibnx/balanceOf");
        })
        .catch((err) => {
          console.log(err);
          if (err == "余额不足") {
            ElMessage.warning(err);
          } else {
            ElMessage.error("fail");
          }
          this.isMinting = false;
        });
      this.isMinting = false;
    },
  },
};
</script>

<style></style>
